@import url('https://fonts/googleapis.com/css2?family=poppins:wght@400;500;600&d');

*{
  font-family: "poppins",sans-serif;
  padding: 0;
  margin: 0;
  outline: none;
}

.container{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #eff2f5;
}

.btn{
  font-size: 1rem;
  font-weight: 500;
  color: #0d315d;
  border: none;
  padding: 0.5rem 3.5rem;
  background-color: #bcd7f8;
}

.btn-left{
  border-radius: 0.2rem 0 0 0.2rem;
}

.btn-right{
  border-radius:0 0.2rem 0.2rem 0;
}

.active{
  color: #ffffff;
  background-color: #207ae8;
}

.card{
  width: 20rem;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border-radius: 0.4rem;
  padding: 1rem;
  margin-top: 1.5rem;
}

textarea{
  min-height: 100px;
  font-size: 0.9rem;
  font-weight: 400;
  background-color: #ffffff;
  border: 2px solid #d2e4fa;
  border-radius: 0.3rem;
  padding: 0.5rem 0.9rem;
}

textarea::placeholder{
  color: #353434;
}

.submit-btn{
  border-radius: 0.2rem;
  margin-top: 1rem;
}

.encrypt-btn{
  color: #ffffff;
  background-color: #207ae8;
}

.decrypt-btn{
  color: #ffffff;
  background-color: #e53471;
}
.error{
  font-size: 15px;
  font-weight: 500;
  color: #e53471;
  margin-top: 10px;
}

.content{
  width: 26rem;
  margin-top: 2rem;
  border: 2px dashed #bababa;
  padding: 1rem 1rem;
  border-radius:  0.5rem;
}

label{
  font-size: 0.8rem;
  font-weight: 500;
  color: #a2a2a2;
}

p{
  font-size: 1rem;
  font-weight: 500;
  margin-top: 0.5rem;
  color: #2e2e2e;
  white-space: pre-wrap;
  word-wrap:  break-word;
}

@media screen and (max-width: 768px) {
  .content {
    width: 80%;
    margin-top: 2rem;
    border: 2px dashed #bababa;
    padding: 1rem 1rem;
    border-radius:  0.5rem;
}
}